<template>
  <canvas id="myCanvas"></canvas>
</template>

<script>
import {
  Shape,
  ExtrudeBufferGeometry,
  Scene,
  PerspectiveCamera,
  ConeGeometry,
  SphereGeometry,
  MeshMatcapMaterial,
  TextureLoader,
  Fog,
  WebGLRenderer,
  Mesh,
} from "three";
// import anime from "animejs/lib/anime.es.js";
function RoundedBox(width, height, depth, radius0, smoothness) {
  let shape = new Shape();
  let eps = 0.00001;
  let radius = radius0 - eps;
  shape.absarc(eps, eps, eps, -Math.PI / 2, -Math.PI, true);
  shape.absarc(eps, height - radius * 2, eps, Math.PI, Math.PI / 2, true);
  shape.absarc(
    width - radius * 2,
    height - radius * 2,
    eps,
    Math.PI / 2,
    0,
    true
  );
  shape.absarc(width - radius * 2, eps, eps, 0, -Math.PI / 2, true);
  let geometry = new ExtrudeBufferGeometry(shape, {
    amount: depth - radius0 * 2,
    bevelEnabled: true,
    bevelSegments: smoothness * 2,
    steps: 1,
    bevelSize: radius,
    bevelThickness: radius0,
    curveSegments: smoothness,
  });

  geometry.center();

  return geometry;
}
//shapes
//geometry
const sphereG = new SphereGeometry(5, 32, 32);
const boxG = RoundedBox(16, 16, 16, 4, 32);
const coneG = new ConeGeometry(12, 16, 8);

window.addEventListener("load", () => {
  console.log("page is fully loaded");

  // Scene
  const scene = new Scene();

  // Camera
  const camera = new PerspectiveCamera(
    20,
    window.innerWidth / window.innerHeight,
    1,
    200
  );
  camera.position.z = 25; // Set camera position
  // scene.background = new Color(0xfffcfc);
  scene.fog = new Fog(0xfff3f5, 0.5, 200);

  // Renderer
  const renderer = new WebGLRenderer({
    alpha: true,
    antialias: true,
    canvas: document.querySelector("#myCanvas"),
    powerPreference: "high-performance",
  });
  renderer.setPixelRatio(window.devicePixelRatio);
  renderer.setSize(window.innerWidth, window.innerHeight);
  renderer.physicallyCorrectLights = true;

  // Make Canvas Responsive
  window.addEventListener("resize", () => {
    renderer.setSize(window.innerWidth / 2, window.innerHeight); // Update size
    camera.aspect = window.innerWidth / window.innerHeight; // Update aspect ratio
    camera.updateProjectionMatrix(); // Apply changes
  });

  //materials
  //new material
  const sphereM = new MeshMatcapMaterial();
  const boxM = new MeshMatcapMaterial();
  const coneM = new MeshMatcapMaterial();
  //loaders
  const sphereMTexture = new TextureLoader().load(
    require("../assets/matcap/matcap-pink.png")
  );
  const boxMTexture = new TextureLoader().load(
    require("../assets/matcap/matcap-brown.jpg")
  );
  const coneMTexture = new TextureLoader().load(
    require("../assets/matcap/matcap-lavendar.png")
  );
  //bump
  //
  // boxM.flatShading = true
  boxM.fog = false;
  //
  coneM.fog = false;

  //assign materials
  sphereM.matcap = sphereMTexture;
  boxM.matcap = boxMTexture;
  coneM.matcap = coneMTexture;

  //mesh
  const sphere = new Mesh(sphereG, sphereM);
  const box = new Mesh(boxG, boxM);
  const cone = new Mesh(coneG, coneM);

  //position
  sphere.position.x = 12;
  sphere.position.y = -3.5;
  sphere.position.z = -30;
  sphere.rotation.y = -0.15;
  //
  box.position.x = 15;
  box.position.y = 2;
  box.position.z = -100;
  //
  coneG.translate(0, -4.5, 0);
  cone.position.x = 35;
  cone.position.y = 14;
  cone.position.z = -125;
  // cone.rotation.x = 3.7;
  cone.rotation.z = 0;

  function easeInSine(x) {
    return 1 - Math.cos((x * 3.147) / 2);
  }

  //add to scene
  // box.matrixAutoUpdate = false
  scene.add(sphere, box, cone);

  //render
  var scrolling;
  window.onscroll = function () {
    // only if we haven't already stacked our function
    if (!scrolling) {
      // raise our flag
      scrolling = true;
      requestAnimationFrame(function () {
        // add the callback function
        box.rotation.y += easeInSine(0.2);
        cone.rotation.y -= easeInSine(0.15);
        // release the flag for next frame
        scrolling = false;
      });
    }
  };

  function render() {
    requestAnimationFrame(render);
    sphere.rotation.y += 0.0005;
    box.rotation.y += 0.001;
    box.rotation.x -= 0.001;
    cone.rotation.y -= 0.001;
    renderer.render(scene, camera);
  }
  render();
});

export default {};
</script>

<style scoped>
canvas {
  z-index: 4;
}
@media only screen and (max-width: 550px) {
  canvas {
    display: none;
  }
}
</style>