<template>
  <canvas ref="bg" id="bg"></canvas>
</template>

<script>
export default {
  data: () => ({
    t: 1,
    vueCanvas: null,
  }),
  methods: {
    init: function () {
      window.requestAnimationFrame(this.draw);
    },
    draw: function () {
      const canvas = this.$refs.bg;
      const ctx = canvas.getContext("2d", { alpha: false });
      ctx.canvas.width = window.innerWidth;
      ctx.canvas.height = window.innerHeight;

      //generic drawing functions:
      //draw line
      function line(x1, y1, x2, y2, color, z) {
        ctx.beginPath();
        ctx.strokeStyle = color;
        ctx.moveTo(x1, y1);
        ctx.bezierCurveTo(x1 + 15, y1 + 17, x2, y2, x1 + 200 + z, y2 + 150);
        ctx.lineTo(x2, y2);
        ctx.stroke();
      }

      ctx.fillStyle = "#fffcfc";
      ctx.fillRect(0, 0, ctx.canvas.width, ctx.canvas.height);
      // creating a gradient
      var x = 0,
        y = 700,
        r = 800;
      var gradient2 = ctx.createRadialGradient(x, y, r, x, y, 200);
      gradient2.addColorStop(0, "white");
      gradient2.addColorStop(1.0, "#FFEAED");

      ctx.beginPath();
      ctx.ellipse(x, y, r, r, Math.PI / 4, 0, 2 * Math.PI);
      ctx.fillStyle = gradient2;
      ctx.fill();

      this.t += 0.00005;
      var gradient = ctx.createRadialGradient(
        ctx.canvas.width / 2,
        500,
        Math.abs(Math.sin(this.t) * ctx.canvas.height),
        Math.sin(this.t) * 100,
        500,
        70
      );

      gradient.addColorStop(0, "rgba(240, 195, 89, 0.7)");
      gradient.addColorStop(0.7, "rgba(239, 152, 157, 0.7)");
      gradient.addColorStop(1.0, "rgba(168, 173, 245, 0.7)");
      // for loop to render lines with gradient
      for (var i = 16; i >= 0; i--) {
        line(
          ctx.canvas.width / 2 - i * 45,
          0,
          500 - i * Math.sin(45) * 100,
          ctx.canvas.height,
          gradient,
          Math.sin(i * 100) * 10
        );
      }

      requestAnimationFrame(this.draw);
    },
  },
  mounted: function () {
    this.init();
  },
};
</script>

